export class ToDo {
    constructor(title, id, complete, dateAdded) {
        this.title = title;
        this.id = id;
        this.complete = complete;
        this.dateAdded = dateAdded;
    }
    toggleComplete() {
        if (this.complete) {
            this.dateCompleted = null;
        }
        else {
            this.dateCompleted = new Date();
        }
        this.complete = !this.complete;
    }
}
