import { ToDo } from "./models/todo.class";
import axios from "axios";
import { writable } from "svelte/store";
export const todoStore = writable([]);
export const loadingStore = writable(false);
const serverUrl = "https://9vnj8jycfk.execute-api.eu-west-2.amazonaws.com/todos";
export const getTodosFromServer = async () => {
    let todosFromServer = await axios.get(serverUrl);
    return todosFromServer.data.map((todo) => {
        return new ToDo(todo.title, todo.id, todo.complete, new Date(todo.dateAdded));
    });
};
export const getTodosAndSet = async () => {
    let todos = await getTodosFromServer();
    todoStore.set(todos);
};
export const toggleLoading = (state) => {
    loadingStore.set(state);
};
export const deleteTodoID = async (id) => {
    let ax = await axios.delete(`${serverUrl}/${id}`);
    return ax.data;
};
export const addTodo = async (title) => {
    const t = await axios.put(serverUrl, { title: title });
    const newTodo = new ToDo(t.data.title, t.data.id, t.data.complete, t.data.dateAdded);
    return newTodo;
};
